import { useState, useContext } from "react"
import { useForm } from "react-hook-form"
import { Link, useParams, useHistory } from "react-router-dom"
import './register.css'
import IcebergImage from './img/Iceberg.png'
import { ReactComponent as GoogleIcon} from './img/google-brands.svg'
import { ReactComponent as FacebookIcon } from './img/facebook-brands.svg'
import Loading from '../Login/img/loading48.gif'
import { submitConsultantRegistrationData } from "../Axios/consulAxios"
import studentFuncs from '../Axios/studentAxios'
import { sendEmailOTP } from "../Axios/consulAxios"
import { ParamContext } from "../ContextReducer"
import { wrapLoginData } from "../DataProcessUtils"

import MetaTags from 'react-meta-tags'

import ActionButton from "../GlobalComponents/Components/ActionButton"


const Register = () => {
    const { register, handleSubmit, formState: { errors } } = useForm()
    const { identity } = useParams()
    const [loading, setLoading] = useState(false)
    const [schoolBoxStyle, setSchoolBoxStyle] = useState({color: 'rgb(212,232,240)'})
    const [yearBoxStyle, setYearBoxStyle] = useState({color: 'rgb(212,232,240)'})
    const context = useContext(ParamContext)
    const history = useHistory()
    const schoolList = ['']
    const registerOnSubmit = async (payload) => {
        console.log(payload)
        if (identity === 'consultant') {
            setLoading(true)
            const { status, data } = await submitConsultantRegistrationData(payload);
            console.log(status, data)
            await context.setInfo({
                type: 'register',
                payload: wrapLoginData(data, 'consultant'),
            })
            context.setLogin(true)
            const otpRequest = await sendEmailOTP({id:data.id})
            console.log(otpRequest.status, otpRequest.msg)
            setLoading(false)
            history.push(`/register-email-otp/${identity}`)
        } else {
            setLoading(true)
            const { status, data, message } = await studentFuncs.studentRegister(payload);
            console.log(status, data, message)
            await context.setInfo({
                type: 'register',
                payload: wrapLoginData(data, 'student'),
            })
            context.setLogin(true)
            const otpRequest = await studentFuncs.sendEmailOTP({id:data.id})
            console.log(otpRequest.status, otpRequest.msg)
            setLoading(false)
            history.push(`/register-email-otp/${identity}`)
        }
    }

    const displayOptions = (ops) => {
        return ops.map((op) => (
            <option value={op}>{op}</option>
        ))
    }

    const emailValidatePattern = {
        value: /^[a-zA-Z0-9._:$!%-]+@[a-zA-Z0-9.-]+.[a-zA-Z]$/,
        message: "Email"
    }
    const phoneValidatePattern = {
        value: /^[0-9]{10,10}$/,
        message: ""
    }
    const passwordValidatePattern = {
        value: /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&_])[A-Za-z\d@$!%*?&_]{8,150}$/,
        message: "1111:@$!%*?&_8"
    }

    const submitButtonContent = () => {
        if (loading) {
            return (
                <img className="register-loading" src={Loading} />
            )
        } else return ''
    }
    if (identity === 'consultant') {
        return (
            <div className="register-main">
                <MetaTags>
                    <title> | Arctics</title>
                </MetaTags>
                <form className="register-content-box" onSubmit={handleSubmit(registerOnSubmit)}>
                    <div className="register-image-block">
                        <img className="register-image" src={IcebergImage} />
                    </div>
                    <div className="register-form">
                        <div className="register-name">
                            <div className="register-firstname">
                                <input className="register-firstname-inputbox" placeholder="" {...register('surname', {required: true})} />
                                {errors.name && <span className="register-error-message" id='name'>!</span>}
                            </div>
                            <div className="register-lastname">
                                <input className="register-lastname-inputbox" placeholder="" {...register('name', {required: true})} />
                                {errors.surname && <span className="register-error-message" id='surname'>!</span>}
                            </div>    
                        </div>
                        <div className="register-institution">
                            <div className="register-school">
                                <select className="register-school-inputbox" placeholder="" 
                                    {...register('school', {required: true, validate: {checkOption: (val)=>(val!=="")}})}
                                    style={schoolBoxStyle} onChange={()=>{setSchoolBoxStyle({})}}
                                >
                                    {<option selected disabled></option>}
                                    {displayOptions(schoolList)}
                                </select>
                                {errors.school && <span className="register-error-message" id='school'>!</span>}
                            </div>
                            <div className="register-grade">
                                <select className="register-grade-inputbox" placeholder="" 
                                    {...register('year', {required: true, validate: {checkOption: (val)=>(val!=="")}})}
                                    style={yearBoxStyle} onChange={()=>{setYearBoxStyle({})}}
                                >
                                    <option selected disabled ></option>
                                    <option value={''} ></option>
                                    <option value={''} ></option>
                                    <option value={''} ></option>
                                    <option value={''} ></option>
                                    <option value={''} ></option>
                                </select>
                                {errors.year && <span className="register-error-message" id='grade'>!</span>}
                            </div>    
                        </div>
                        <div className="register-email">
                            <input className="register-email-inputbox" placeholder="" {...register('email', {required: 'Email!', pattern: emailValidatePattern})}/>
                            {errors.email?.message && <span className="register-error-message" id='email'>{errors.email.message}</span>}
                        </div>
                        <div className="register-phone">
                            <input className="register-phone-inputbox" placeholder="0912345678" {...register('mobile', {required: '!', pattern: phoneValidatePattern})} />
                            {errors.mobile?.message && <span className="register-error-message" id='phone'>{errors.mobile.message}</span>}
                        </div>
                        <div className="register-password">
                            <input type='password' className="register-password-inputbox" placeholder="" {...register('password', {required: '!', pattern: passwordValidatePattern})} />
                            {errors.password?.message && <span className="register-error-message" id='pwd'>{errors.password.message}</span>}
                        </div>
                        <div className="register-submit">
                            <button type='submit' className="register-submit-button">{submitButtonContent()}</button>
                            <p className="register-to-login"><Link to='/login'><span className="register-login-link"></span></Link></p>
                        </div>
                    </div>
                </form>
            </div>
        )
    } else {
        return (
            <div className="register-main">
                <MetaTags>
                    <title> | Arctics</title>
                </MetaTags>
                <form name="registerForm" className="register-content-box" onSubmit={handleSubmit(registerOnSubmit)}>
                    <div className="register-image-block">
                        <img className="register-image" src={IcebergImage} />
                    </div>
                    <div className="register-form">
                        <div className="register-name">
                            <div className="register-firstname">
                                <input className="register-firstname-inputbox" placeholder="" {...register('surname', {required: true})} />
                                {errors.name && <span className="register-error-message" id='name'>!</span>}
                            </div>
                            <div className="register-lastname">
                                <input className="register-lastname-inputbox" placeholder="" {...register('name', {required: true})} />
                                {errors.surname && <span className="register-error-message" id='surname'>!</span>}
                            </div>    
                        </div>
                        <div className="register-institution">
                            <div className="register-school">
                                <input className="register-school-inputbox" placeholder="" 
                                    {...register('school', {required: true})}
                                />
                                {errors.school && <span className="register-error-message" id='school'>!</span>}
                            </div>
                            <div className="register-grade">
                                <select className="register-grade-inputbox" placeholder="" 
                                    {...register('year', {required: true, validate: {checkOption: (val)=>(val!=="")}})}
                                    style={yearBoxStyle} onChange={()=>{setYearBoxStyle({})}}
                                >
                                    <option selected disabled ></option>
                                    <option value={''} ></option>
                                    <option value={''} ></option>
                                    <option value={''} ></option>
                                </select>
                                {errors.year && <span className="register-error-message" id='grade'>!</span>}
                            </div>    
                        </div>
                        <div className="register-email">
                            <input className="register-email-inputbox" placeholder="" {...register('email', {required: 'Email!', pattern: emailValidatePattern})}/>
                            {errors.email?.message && <span className="register-error-message" id='email'>{errors.email.message}</span>}
                        </div>
                        <div className="register-phone">
                            <input className="register-phone-inputbox" placeholder="0912345678" {...register('mobile', {required: '!', pattern: phoneValidatePattern})} />
                            {errors.mobile?.message && <span className="register-error-message" id='phone'>{errors.mobile.message}</span>}
                        </div>
                        <div className="register-password">
                            <input type='password' className="register-password-inputbox" placeholder="" {...register('password', {required: '!', pattern: passwordValidatePattern})} />
                            {errors.password?.message && <span className="register-error-message" id='pwd'>{errors.password.message}</span>}
                        </div>
                        <div className="register-submit">
                            {/* <button type='submit' className="register-submit-button">{submitButtonContent()}</button> */}
                            <ActionButton label="" loading={loading} type='submit'/>
                            <p className="register-to-login"><Link to='/login'><span className="register-login-link"></span></Link></p>
                        </div>
                    </div>
                </form>
            </div>
        )
    }
}

export default Register

